//styles
import styles from './TimeTable.module.css';
//useHook
import { useNavigate } from 'react-router-dom';
import { useEffect, useState } from 'react';
import React from 'react';
import Times from './Times';

function TimeTable() {
  const [ablebtn, setAblebtn] = useState(true); //   (true   !)
  const navigate = useNavigate();
  //         
  const morning = ['09:00', '10:00', '11:00', '12:00'];
  const afternoon = ['13:00', '14:00', '15:00', '16:00', '17:00'];
  const night = ['18:00', '19:00', '20:00'];

  //21:00-08:30    
  const Now = new Date(); //    -> Tue Aug 23 2022 16:33:51 GMT+0900
  const NowHour = Now.getHours();
  const NowMins = Now.getMinutes();

  function pluszero(times) {
    let time = times.toString(); //   
    if (time.length < 2) {
      time = '0' + time; //  0 
      return time;
    } else {
      return time;
    }
  }
  const nowHour = pluszero(NowHour);
  const nowMins = pluszero(NowMins);
  const nowTime = nowHour + nowMins;
  //console.log(nowTime);

  const startTime = '0830';
  const endTime = '2100';
  useEffect(() => {
    if (startTime > nowTime || endTime < nowTime) {
      setAblebtn(false);
    } else {
      setAblebtn(true);
    }
  }, []); //useEffect   
  //console.log(ablebtn);

  //  alert 
  const BookingConfirm = () => {
    if (startTime > nowTime || endTime < nowTime) {
      alert(
        '   !\n8:30 21:00  .'
      );
    } else {
      alert('      :)');
      navigate('/mypage');
    }
  };

  return (
    <div className={styles.timewrap}>
      <h6 className={styles.time}>   </h6>
      <Times/>
      <button
        className={ablebtn === true ? styles.bookbtn : styles.bookbtnOff}
        onClick={BookingConfirm}
      >
        
      </button>
    </div>
  );
}

export default TimeTable;
